(() => {
  const wrap = document.querySelector(`.seo__text`);
  const button = document.querySelector(`.seo__button`);

  wrap.classList.remove(`seo__text--visible`);
  wrap.classList.add(`seo__text--hidden`);

  button.addEventListener(`click`, () => {
    if (wrap.classList.contains(`seo__text--hidden`)) {
      wrap.classList.add(`seo__text--visible`);
      wrap.classList.remove(`seo__text--hidden`);
      button.textContent = `Скрыть`;
    } else if (wrap.classList.contains(`seo__text--visible`)) {
      wrap.classList.add(`seo__text--hidden`);
      wrap.classList.remove(`seo__text--visible`);
      button.textContent = `Читать полностью`;
    }
  });
})();
